import { getSpecialData } from '../../network/index.js';
export default {
	data() {
		return {
			keywordList: []
		};
	},
	onLoad(){
		this.getData()
	},
	filters:{
		getImgUrl(val){
			return JSON.parse(val)[0].url
		}
	},
	methods: {
		toSerach() {
			uni.navigateTo({
				url:'/pages/search/search'
			})
		},
		toDetails(id){
			uni.navigateTo({
				url:'/pages/details/details?id='+id
			})
		},
		getData(){
			getSpecialData().then(res=>{
				console.log(res)
				if(res.data.code == 100 ){
					this.keywordList = res.data.info
				}
			})
		}
	}
};
